/**
 * Workaround to allow popover to be rendered beyond the sb containerâ€™s bounds
 */
export function fixFilterZIndex(canvasElement: HTMLElement) {
  window.requestAnimationFrame(() => {
    // unset overflow styles
    const container = canvasElement.closest('.sbdocs-preview') as HTMLDivElement;
    if (!container) {
      return;
    }
    container.style.overflow = 'unset !important';
    (container.querySelector('.docs-story') as HTMLDivElement).style.overflow = 'unset';
    (container.querySelector('.docs-story div:first-of-type') as HTMLDivElement).style.overflow = 'unset';

    // set z-index of 1
    canvasElement.querySelectorAll('.market-filter').forEach((filter) => {
      filter.addEventListener('marketFilterExpandedChanged', ({ detail }: CustomEvent) => {
        container.style.zIndex = detail ? '1' : null;
        (container.querySelector('.docs-story div:first-of-type') as HTMLDivElement).style.zIndex = detail ? '2' : '1';
      });
    });
  });
}
